body {
  margin: 0;
  box-sizing: border-box;
  padding-left: 16px;
  font-family: Roboto, 'Helvetica Neue', sans-serif;
}

.card {
  margin-left: 10px;
  width: 198px;
  height: 406px;
  top: 1px;
  left: 1px;
  border-radius: 5px;
  border: rgba(243, 243, 243, 1) solid 1px;
}

.imac {
  margin-left: 19px;
  width: 160px;
  height: 134px;
  margin-right: 20px;
  margin-top: 32px;
}

.title {
  color: rgba(6, 11, 53, 1);
  margin-left: 16px;
  margin-top: 36px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.review {
  margin-left: 16px;
  color: rgba(97, 96, 112, 1);
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.rate {
  margin-left: 16px;
  margin-top: 16px;
  margin-bottom: 24px;
  height: 16px;
  width: 166px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.reviews {
  margin-top: 2px;
  font-weight: 400;
  color: rgba(6, 11, 53, 1);
  font-size: 10px;
  line-height: 14px;
}

.price {
  margin-left: 16px;
  width: 166px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
}

.price__word {
  color: rgba(97, 96, 112, 1);
  font-size: 12px;
  line-height: 18px;
}

.price__number {
  color: rgba(6, 11, 53, 1);
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
}

.buy {
  margin-left: 16px;
  text-decoration: none;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  border-radius: 5px;
  display: flex;
  width: 166px;
  height: 40px;
  color: rgba(255, 255, 255, 1);
  background-color: rgba(0, 172, 220, 1);
  justify-content: center;
  align-items: center;
}

.buy:hover {
  font-weight: 700;
  background-color: #fff;
  border: 1px solid rgba(0, 172, 220, 1);
  color: rgba(0, 172, 220, 1);
}
